import Link from "next/link";
import Header from "@/components/header";
import Footer from "@/components/footer";
// UTILS: slug generator
function slugify(text: string) {
  return text.toLowerCase().replace(/[^a-z0-9\s-]/g, "").trim().replace(/\s+/g, "-");
}

// AMENITIES DATA
const amenities = [
  { icon: "üèä", name: "Swimming Pool", description: "Olympic-sized heated pool" },
  { icon: "üçΩÔ∏è", name: "Fine Dining", description: "5-star restaurant and bar" },
  { icon: "üíÜ", name: "Spa & Wellness", description: "Full-service luxury spa" },
  { icon: "üèãÔ∏è", name: "Fitness Center", description: "State-of-the-art gym" },
  { icon: "üìö", name: "Business Center", description: "Full conference facilities" },
  { icon: "üöó", name: "Valet Parking", description: "Complimentary valet service" },
].map((item) => ({ ...item, slug: slugify(item.name) }));

// ‚úÖ Async server component
export default async function AmenityDetail({ params }: { params: Promise<{ slug: string }> }) {
  const { slug } = await params; // UNWRAP the promise

  const amenity = amenities.find((a) => a.slug === slug);

  if (!amenity) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-2xl font-bold mb-4">Amenity Not Found</h1>
          <Link href="/amenities">
            <button className="px-6 py-2 bg-blue-600 text-white rounded-lg">
              Back to Amenities
            </button>
          </Link>
        </div>
      </div>
    );
  }

  return (
    <><Header />
    <div className="max-w-4xl mx-auto py-20 px-6">
      <h1 className="text-4xl font-bold flex items-center gap-4">
        <span className="text-5xl">{amenity.icon}</span>
        {amenity.name}
      </h1>

      <p className="mt-6 text-lg text-muted-foreground">{amenity.description}</p>

      <Link href="/amenities" className="mt-8 inline-block text-blue-600 hover:underline">
        ‚Üê Back to Amenities
      </Link>
    </div>
    Footer
    </>
  );
}
